import React, { Component } from 'react';

export class Parking extends Component {
  props: any;
  render() {
    return (
      <div className={this.GetDivClassName()}>
        <div className="fw">
          <div className="wc1">
            <h1>
              <a href={this.GetUrl()}>{this.props.action}</a>
            </h1>
          </div>
          <div className="waddress">
            <h4>{this.props.startAddress}</h4>
          </div>
          <div className="waddress">
            <h4>{this.props.endAddress}</h4>
          </div>
        </div>
        <div className="b1 fw">
          <div className="wc1">
            Date: {this.FormatUnixTimestamp(this.props.startDate)}
            <br />
            Duration: {this.props.minuteDiff} Min
            <br />
            Odometer: {Math.round(this.props.odometer * 10) / 10} km
          </div>
          <div className="w33">
            kWh: {Math.round(this.props.kWh * 10) / 10}
            <br />
            Range Diff: {Math.round(this.props.rangeDiff * 10) / 10}km
            <br />
            SOC: {this.props.soc}% / SOC Diff: {this.props.socDiff}%
          </div>
          <div className="w33">{this.DrawLabelIfnotNull('Ø Temperature: ', this.props.outsideTempAvg, '°C')}</div>
        </div>
      </div>
    );
  }

  GetUrl() {
    if (this.props.action === 'Drive') {
      //return this.props.urlprefix + 'd/zm7wN6Zgz/verbrauch?from=' + this.props.startDate + '&to=' + this.props.endDate;
      return 'd/zm7wN6Zgz/verbrauch?from=' + this.props.startDate + '&to=' + this.props.endDate;
    } else if (this.props.action === 'Parking') {
      return 'd/zm7wN6Zgz/verbrauch?from=' + this.props.startDate + '&to=' + this.props.endDate;
    } else if (this.props.action === 'Charging') {
      return 'd/BHhxFeZRz/laden?orgId=1&from=' + this.props.startDate + '&to=' + this.props.endDate;
    } else if (this.props.action === 'Missing') {
      return '#';
    }

    return '#';
  }

  DrawLabelIfnotNull(label: string, n: number, suffix: string) {
    if (n != null) {
      let t = Math.round(n * 10) / 10;
      return label + t + suffix;
    }

    return '';
  }

  GetDivClassName() {
    if (this.props.action === 'Drive') {
      return 'cb blgr';
    } else if (this.props.action === 'Parking') {
      return 'cb blbl';
    } else if (this.props.action === 'Charging') {
      return 'cb blge';
    } else if (this.props.action === 'Missing') {
      return 'cb blrt';
    }

    return 'cb';
  }

  FormatUnixTimestamp(timestamp: number) {
    let d = new Date(timestamp);
    return d.toLocaleString();
  }
}
